
.selectMonth {
    display: inline-block;
    height: 50px;
    padding-right: 8px;
}
.selectMonth::after {
  content: '';
  display: inline-block;
  margin-left: 8px; /* 12px扣掉inline-block 4px */
  width: 9px;
  height: 9px;
  border-right: 2px solid #3ea4ff;
  border-bottom: 2px solid #3ea4ff;
  -webkit-transform: rotate(45deg) translateY(-4.5px);
          transform: rotate(45deg) translateY(-4.5px);
}
#monthPicker {
  position: relative;
  z-index: 999;
}

/* 調整日曆寬度 */
#monthPicker .layui-laydate {
  left: 0 !important; /* 複寫laydate預設的值，一定要加!important */
  margin: 0 12px auto 12px;
  border-radius: 5px;
}
#monthPicker .layui-laydate .layui-laydate-main {
  width:100%;
}
#monthPicker .layui-laydate-main .layui-laydate-content table {
  width:100%;
}

/* 日曆外框圓角 */
#monthPicker .layui-laydate-main .layui-laydate-header {
  border-radius: 5px 5px 0 0 ;
}
#monthPicker .layui-laydate-main .layui-laydate-content {
  border-radius: 0 0 5px 5px;
}

/* 日曆表頭 */
#monthPicker .layui-laydate .layui-laydate-header {
  color: #fefefe;
  font-size: 18px;
  background: #2fdafd;
  background: -webkit-linear-gradient(left,#2fdafd 0%,#3abdff 100%);
  background: -webkit-linear-gradient(left, #2fdafd 0%, #3abdff 100%);
  background: linear-gradient(to right, #2fdafd 0%, #3abdff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(
    startColorstr="#2fdafd",
    endColorstr="#3abdff",
    GradientType=1
  );
}
#monthPicker .layui-laydate .layui-laydate-header i {
  color: #ffffff;
}

/* 月份樣式 */
#monthPicker .layui-laydate-list {
  position: relative;
  padding: 0;
}
#monthPicker .laydate-month-list li {
  margin: 6px 0;
}

/* 目前點選 */
#monthPicker .layui-laydate .layui-laydate-main .layui-laydate-content .layui-this {
  border-radius: 5px;
  color: #ffffff;
  background: #2fdafd;
  background: -webkit-linear-gradient(left,#2fdafd 0%,#3abdff 100%);
  background: -webkit-linear-gradient(left, #2fdafd 0%, #3abdff 100%);
  background: linear-gradient(to right, #2fdafd 0%, #3abdff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(
    startColorstr="#2fdafd",
    endColorstr="#3abdff",
    GradientType=1
  );
}

/* 取消切換日期的轉場效果 */
#monthPicker .layui-laydate-list li {
  transition-duration: 0s;
  -moz-transition-duration: 0s; /* Firefox 4 */
  -webkit-transition-duration: 0s; /* Safari 和 Chrome */
  -o-transition-duration: 0s; /* Opera */
}

/* 其他日期 */
#monthPicker .layui-laydate-content td {
  color: #333333;
  padding: 0;
  cursor: none;
}

/* 取消確定按鈕樣式 */
#monthPicker .laydate-footer-btns span {
  border: 0;
  border-radius: 5px;
  font-size: 15px;
}
.select_month{
  background-color: rgba(0,0,0,0);
  position: relative;
  z-index: 2;
  border: 0px;
  margin: 0px;
  padding: 0px 25px 0px 0px;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  text-align: center;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: 18px;
  color: #3ea4ff;
  outline: none;
}
.gp_selmonth::after{
  position: absolute;
  content: '';
  display: inline-block;
  margin-left: 25px;
  width: 9px;
  height: 9px;
  border-right: 2px solid #3ea4ff;
  border-bottom: 2px solid #3ea4ff;
  -webkit-transform: rotate(45deg) translateY(-4.5px);
  transform: rotate(45deg) translateY(-4.5px);
  top: 19px;
  left: 50%;
}



