
.common-header .header{
  height: 44px;
  padding-top: 20px;
  width: 100vw;
  font-size: 18px;
  line-height: 44px;
  text-align: center;
  color: #ffffff;
}
.common-header #back {
  position: absolute;
  height: 15px;
  line-height: 15px;
  left: 12px;
  top: 33px;
}
.common-header #icon-back {
  width: 8.5px;
  height: 14px;
}
.common-header #back span{
  display: inline-block;
  color: #ffffff;
  font-size: 15px;
  vertical-align: top;
}



