























































/* 調整日曆寬度 */
#multiDatePicker .layui-laydate {
  display: block;
  border-radius: 5px;
}
#multiDatePicker .layui-laydate .layui-laydate-main{
  width:100%;
}
#multiDatePicker .layui-laydate .layui-laydate-main .layui-laydate-content table{
  width:100%;
}
/* 日曆圓角 */
#multiDatePicker .layui-laydate .layui-laydate-header {
  border-radius: 5px 5px 0 0 ;
}
#multiDatePicker .layui-laydate .layui-laydate-content {
  border-radius: 0 0 5px 5px;
}
/* 日曆表頭 */
#multiDatePicker .layui-laydate .layui-laydate-header {
  color: #fefefe;
  font-size: 18px;
  background: #2fdafd;
  background: -webkit-linear-gradient(left,#2fdafd 0%,#3abdff 100%);
  background: -webkit-linear-gradient(left, #2fdafd 0%, #3abdff 100%);
  background: linear-gradient(to right, #2fdafd 0%, #3abdff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(
    startColorstr="#2fdafd",
    endColorstr="#3abdff",
    GradientType=1
  );
}
/* 目前點選 */
#multiDatePicker .layui-laydate .layui-this {
  color: #333333 !important;
  background-color: rgba(255, 255, 255, 0) !important;
}
/* 標記樣式 */
#multiDatePicker .laydate-day-mark {
  position: relative;
  display: block;
  margin: auto;
  width: 27px;
  height: 27px;
  line-height: 27px;
  font-size: 15px;
  border-radius: 50%;
  color: #ffffff;
  background: #2fdafd;
  background: -webkit-linear-gradient(left,#2fdafd 0%,#3abdff 100%);
  background: -webkit-linear-gradient(left, #2fdafd 0%, #3abdff 100%);
  background: linear-gradient(to right, #2fdafd 0%, #3abdff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(
    startColorstr="#2fdafd",
    endColorstr="#3abdff",
    GradientType=1
  );
}
.layui-laydate-content td:hover, .layui-laydate-list li:hover {
  background-color: transparent;
}
#multiDatePicker .laydate-day-mark::after {
  width: 0px;
  height: 0px;
}
/* 其他日期 */
#multiDatePicker .layui-laydate-content td {
  color: #333333;
  padding: 0;
  cursor: none;
}



