
.check-in-option .top-navbar{
  height: 30px;
  font-size: 15px;
  color: #333333;
  width: 100vw;
  box-shadow: 3px 3px 5px 0px #e0e0e0;
  box-sizing: border-box;
  padding: 0 15px;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}
.check-in-option .top-navbar li{
  list-style-type: none;
  box-sizing: border-box;
  margin-right: 25px;
  position: relative;
}
.check-in-option .top-navbar li:last-child{
  margin-right: 0;
}
.check-in-option .top-navbar .navbarActive {
  color: #3abdff;
}
.check-in-option .top-navbar .navbarActive::before {
  border-bottom: 2px solid #67b7ff;
  content: '';
  position: absolute;
  width: 60px;
  height: 2px;
  top: 22px;
  left: 0;
  right: 0;
  margin: auto;
}



