
html,
body {
  width: 100vw;
  height: 100vh;
  /*文字禁止选中和复制*/
  -moz-user-select: none;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */ /* Firefox */ /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently*/
}
#app {
  width: 100%;
  height: 100%;
  /* 防止页面滑动 */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* 子路由頁面 */
.router-view-without-foot {
  position: absolute;
  height: calc(100vh - 84px);
  top: 84px;
  left: 0;
  bottom: 0px;
  /* width: calc((100vh - 84px) * 0.57); */
  width: 100vw;
  overflow-y: hidden;
  overflow-x: hidden;
  background: #fff;
  /* -webkit-overflow-scrolling: touch; */
}

/* 子路由頁面 */
.router-view-with-foot {
  position: absolute;
  /* height: calc(100vh - 49px - 84px); */
  height: calc(100vh - 49px - 84px);
  width: 100vw;
  /* width: calc((100vh - 49px - 84px) * 0.57); */
  left: 0px;  
  top: 84px;
  bottom: 65px;
  overflow-y: scroll;
  overflow-x: hidden;
  background: #fff;
  /* -webkit-overflow-scrolling: touch; */
}
@-webkit-keyframes slideInLeft {
from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes slideInLeft {
from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
.slideInLeft {
  -webkit-animation: slideInLeft 0.5s forwards;
          animation: slideInLeft 0.5s forwards;
}
@-webkit-keyframes slideInRight {
from {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
to {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
}
@keyframes slideInRight {
from {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
to {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
}
.slideInRight {
  -webkit-animation: slideInRight 0.5s forwards;
          animation: slideInRight 0.5s forwards;
}

/* vux - loading */
.vux-loading-no-text .weui-toast {
  top: calc((100vh - 7.6em) / 2);
}



