
.blue-tick-list[data-v-b28b84a2] {
  /* max-height: 400px;
  overflow: scroll; */
}
.list-box[data-v-b28b84a2]{
  position: relative;
  padding-top: 12px;
  padding-bottom: 12px;
  width: 96%;
  display: block;
  margin: 5px auto;
  background-repeat: no-repeat;
  background-size: 7px 100%;
  border-radius: 5px;
  background: #ffffff;
  box-shadow: 3px 3px 5px 0px #e0e0e0;
}

/* 列表清單背景 */
.list-box[data-v-b28b84a2]:before {
  content: '';
  position: absolute;
  width: 7px;
  height: 100%;
  background: #2fdafd; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #2fdafd 0%,#3abdff 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #2fdafd 0%,#3abdff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(
      startColorstr="#2fdafd",
      endColorstr="#3abdff",
      GradientType=1
    ); /* IE6-9 */
  border-radius: 6px 0 0 6px;
  top:0;
  left:0;
}
.list-box > ul[data-v-b28b84a2]{
  padding-left: 18px;
  font-size: 15px;
  color: #1b1b1b;
}
.list-box .list-title[data-v-b28b84a2] {
  font-size: 15px;
  color: #333333;
  height: 15px;
  margin-bottom: 8px;
  vertical-align: middle;
}
.list-box .list-addr[data-v-b28b84a2] {
  font-size: 12px;
  color: #666666;
  max-width: 85%;
  vertical-align: middle;
}
.list-box .radio-div[data-v-b28b84a2] {
  top: 29%;
  position: absolute;
  max-width: 17px;
  max-height: 16px;
  right: 17px;
}
.vux_scroller-content[data-v-b28b84a2] {
  padding-bottom: 10px;
}

/* radio button 樣式 */
.list-box input[type="radio"][data-v-b28b84a2]{
	display: none;
}
.list-box input[type="radio"] + label[data-v-b28b84a2]{
	display: inline-block;
  background: url('../../../assets/img/common/choose_n@2x.png') no-repeat;
	background-size: 16px 16px;
	background-position: left;
}
.list-box label[data-v-b28b84a2]{
	padding-left: 20px;
	padding-right: 10px;
}
.list-box input[type="radio"]:checked + label[data-v-b28b84a2]{
	display: inline-block;
	background: url('../../../assets/img/common/choose_y@2x.png') no-repeat;
	background-size: 16px 16px;
	background-position: left;
}



