
.storeFilter[data-v-3f321ac0] {
  /* position: relative;
  height: 50px;
  height: 99px; */
  /* position: fixed; */
  position: absolute;
  width: 100vw;
  left: 0;
  z-index: 20;
}
.mask[data-v-3f321ac0] {
  background: rgba(0, 0, 0, 0.3);
  position: fixed;
  width: 100vw;
  /* top: 64px; */
  top: 84px;
  height: calc(100vh - 84px);
  bottom: 0;
  z-index: 1;
}
/* 集團終端、建議終端按鈕區塊 */
.typeBlock[data-v-3f321ac0] {
  display: -webkit-flex;
  display: flex;
  display: -webkit-box;
  display: -ms-flexbox;
  background: #ffffff;
  position: relative;
  z-index: 3;
}
.typeBtn[data-v-3f321ac0] {
  -webkit-flex: 1;
          flex: 1;
  -ms-flex: 1;
  -webkit-box-flex: 1;
  text-align: center;
  color: #666666;
  font-size: 14px;
  height: 50px;
  line-height: 50px;
  position: relative;
}
.typeBtn[data-v-3f321ac0]:nth-child(1)::after {
  content: '';
  height: 43px;
  width: 1px;
  background: #e6e6e6;
  position: absolute;
  right: 0;
  top: 3.5px;
}
.typeSelect[data-v-3f321ac0] {
  color: #3ea4ff;
}
.iconFilter[data-v-3f321ac0] {
  width: 18px;
  height: 16px;
  position: absolute;
  top: 17px;
  right: 24px;
}
/* 篩選器區塊 */
.optionBlock[data-v-3f321ac0] {
  position: absolute;
  top: 50px;
  z-index: 5;
  background: #ffffff;
}
li[data-v-3f321ac0] {
  /* margin: 10px auto auto 24px; */
  margin-top: 10px;
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  box-sizing: border-box;
  font-size: 0;
}
label[data-v-3f321ac0] {
  margin-right: 17px;
  font-size: 14px;
  line-height: 30px;
  color: #666666;
  width: 26%;
  display: inline-block;
}
.commonSelect[data-v-3f321ac0] {
  display: inline-block;
  vertical-align: top;
}
.infoInput[data-v-3f321ac0] {
  background: #f7f7f7;
  border-radius: 15px;
  height: 30px;
  border: none;
  outline: none;
  padding-left: 10px;
  box-sizing: border-box;
}
.commonSelect[data-v-3f321ac0],
.infoInput[data-v-3f321ac0] {
  width: 67%;
}
.btn-lg[data-v-3f321ac0] {
  display: block;
  margin: 37px auto 25px;
}
/* 搜索區塊 */
.searchBlock[data-v-3f321ac0] {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding: 8px 25px;
  background: #eeeeee;
}
.searchInput[data-v-3f321ac0] {
  width: 100%;
  height: 33px;
  padding-left: 33px;
  border-radius: 4px;
  background: #e2e2e2 url('../../../assets/img/common/search@2x.png') no-repeat;
  background-size: 13px 13px;
  background-position: 13px;
  color: #bcbcbc;
  font-size: 14px;
  border: none;
  box-sizing: border-box;
  outline: none;
}
.search_btn[data-v-3f321ac0] {
  border: 1px solid #666666;
  color: #666666;
  width: 50px;
  /* background: lightslategray; */
  /* color: white; */
  border-radius: 5px;
}
/* iphoneX 設置 */
@media only screen and (width: 375px) and (height: 856px) and (-webkit-min-device-pixel-ratio: 3) {
.mask[data-v-3f321ac0] {
    top: 130px;
    height: calc(100vh - 130px);
    bottom: 0;
    z-index: 1;
}
}



