


































































































































/* 整體 外框 */
.container[data-v-4906d350] {
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
}
/* 頭部區塊 */
.login-bg[data-v-4906d350] {
  /* padding-top: 40px;
  background: #aff2f2; */
  height: 40px;
}
/* 頭部 底圖 */
.login-head-image[data-v-4906d350] {
  width: 100vw;
  height: 288px;
}
/* 內容 區塊 */
.login-content-container[data-v-4906d350] {
  width: 100%;
  min-height: 100px;
  margin-top: 305px;
  background-color: #ffffff;
}
/* input 與 placeholder */
[data-v-4906d350]::-webkit-input-placeholder,
.account input[data-v-4906d350] {
  vertical-align: text-top;
  color: #999;
  font-size: 15px;
}
/* 帳號 input */
.login-info input[data-v-4906d350] {
  width: 60vw;
  height: 40px;
  line-height: 20px;
  font-size: 18px;
  padding: 0px 0px 0px 40px;
  border: none;
  outline: none;
  border-bottom: 1px solid #999;
  border-radius: 0;
  margin: 5px 50px;
  /* text-indent: 30px; */
}
.login-info .account-input[data-v-4906d350] {
  background: url(../../../assets/img/login/login_icon_user.png) no-repeat;
  background-size: 24px 29.5px;
  background-position: 5px;
}
.login-info .password-input[data-v-4906d350] {
  background: url(../../../assets/img/login/login_icon_code.png) no-repeat;
  background-size: 20.5px 28px;
  background-position: 5px;
}
/* 按鈕 區塊 */
.bottom_container[data-v-4906d350] {
  width: 100%;
  min-height: 50px;
  /* height: 188px; */
  background-color: #ffffff;
  /* align-items: flex-start;
  justify-content: center; */
}
.bottom_container p[data-v-4906d350] {
  position: absolute;
  right: 0;
  bottom: 0;
  color: lightgray;
  /* align-items: flex-start;
  justify-content: center; */
}
.button_container[data-v-4906d350] {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
/* 按鈕 */
.button_login[data-v-4906d350] {
  margin-top: 65px;
  width: 250px;
  height: 44px;
  line-height: 44px;
  border-radius: 5px;
  text-align: center;
  color: white;
  border: 5px;
  background-image: -webkit-linear-gradient(top, #5ccdff, #5888ff);
  background-image: linear-gradient(to bottom, #5ccdff, #5888ff);
}
/* 按鈕 點擊 */
.button_login[data-v-4906d350]:active {
  background-image: -webkit-linear-gradient(top, #00a7e4, #447add);
  background-image: linear-gradient(to bottom, #00a7e4, #447add);
}



