
#datePicker {
  /* width: calc(100vw - 24px);
  margin: 12px 12px 0; */
  width: 94%;
  /* height: 100%; */
  margin: 12px auto 0;
  border-radius: 5px
}

/* 調整日曆寬度 */
.layui-laydate {
  display: block;
  border-radius: 5px;
}
.layui-laydate .layui-laydate-main{
  width:100%;
}
.layui-laydate .layui-laydate-main .layui-laydate-content table{
  width:100%;
}

/* 日曆圓角 */
.layui-laydate .layui-laydate-header {
  border-radius: 5px 5px 0 0 ;
}
.layui-laydate .layui-laydate-content {
  border-radius: 0 0 5px 5px;
}

/* 日曆表頭 */
.layui-laydate .layui-laydate-header {
  color: #fefefe;
  font-size: 18px;
  background: #2fdafd;
  background: -webkit-linear-gradient(left,#2fdafd 0%,#3abdff 100%);
  background: -webkit-linear-gradient(left, #2fdafd 0%, #3abdff 100%);
  background: linear-gradient(to right, #2fdafd 0%, #3abdff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(
    startColorstr="#2fdafd",
    endColorstr="#3abdff",
    GradientType=1
  );
}

/* 目前點選 */

/* #datePicker .layui-laydate .layui-this {
  color: #333333 !important;
  background-color: rgba(255, 255, 255, 0) !important;
} */
#datePicker .layui-laydate .layui-this {
  position: relative;
  display: block;
  margin: auto;
  width: 27px;
  height: 27px;
  line-height: 27px;
  font-size: 15px;
  border-radius: 50%;
  color: #ffffff;
  background: #2fdafd !important;
}
#datePicker .layui-laydate .layui-this::after {
  width: 0px;
  height: 0px;
}

