

































/* 列表清單樣式 */
.record_list .list-box[data-v-9b27744a] {
  position: relative;
  box-sizing: border-box;
  font-size: 0;
  padding-top: 8px;
  padding-bottom: 8px;
  /* width: 100%; */
  width: 93%;
  display: block;
  /* margin: 0px auto 5px; */
  margin: 5px auto;
  background-repeat: no-repeat;
  background-size: 7px 100%;
  border-radius: 5px;
  background: #ffffff;
  box-shadow: 3px 3px 5px 0px #e0e0e0;
}
/* 列表清單背景 */
.record_list .list-box[data-v-9b27744a]:before {
  content: '';
  position: absolute;
  width: 7px;
  height: 100%;
  background: #2fdafd; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #2fdafd 0%,#3abdff 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #2fdafd 0%,#3abdff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(
      startColorstr="#2fdafd",
      endColorstr="#3abdff",
      GradientType=1
    ); /* IE6-9 */
  border-radius: 6px 0 0 6px;
  top:0;
  left:0;
}
.list-box > .list-type[data-v-9b27744a] {
  font-size: 15px;
  color: #333333;
  padding-left: 12px;
}
.list-box > .list-status[data-v-9b27744a] {
  font-size: 12px;
  color: #666666;
  padding-left: 15px;
}
.list-box > .list-date[data-v-9b27744a] {
  font-size: 12px;
  padding: 5px 0 0 12px;
  color: #666666;
}
.list-box > .arrow-next[data-v-9b27744a] {
  height: 14px;
  width: 8.5px;
  position: absolute;
  right: 13px;
  top: 40%;
}



