
.bg-gray {
  background: gray;
}

/* .attendance-page {
  width: 100vw;
  height: calc(100vh - 63px);
  position: fixed;
  top: 63px;
  left: 0;
  z-index: 100;
  margin: 0;
} */
.attendance-page .btn-right {
  width: 50px;
  font-size: 11px;
  text-align: center;
  color: #ffffff;
  position: absolute;
  top: 19.5px;
  right: 12px;
  /* z-index: 20; */
}
.attendance-page .btn-right img {
  width: 17.5px;
  height: 17.5px;
}
.attendance-page .clock-area {
  position: relative;
  width: 100vw;
  height: 246px;
}
.attendance-page .clock-text {
  text-align: center;
  color: #ffffff;
  font-size: 12px;
  margin-bottom: 15px;
}
.attendance-page .arc-bg {
  position: absolute;
  bottom: -1px;
  height: 37px;
  width: 100vw;
}
.attendance-page .clock-canvas {
  width: 150px;
  height: 150px;
  display: block;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 50%;
}
.attendance-page .attendance-btn-area {
  height: 50px;
  margin-top: 28px;
}

/* 按鈕 */
.attendance-page .attendance-btn-area button {
  display: block;
  margin: 0 auto;
}
.attendance-page .attendance-btn-area .btn-area {
  float: left;
  width: 50%;
}
.attendance-page .manu-tab {
  background: #efa68eb0;
  height: 46px;
  width: 100vw;
  position: fixed;
  bottom: 0;
  left: 0;
}
.attendance-page .attendance-info {
  max-height: calc(100vh - 520px);
  overflow-y: scroll;
  overflow-x: hidden;
  margin-top: 10%;
}
.attendance-page .attendance-info-text{
  margin-top: 16px;
  margin-left: 65%;
  /* position: absolute;
  right: 7%; */
}
.attendance-page .attendance-info-text span {
  display: inline-block;
  font-size: 14px;
  color: #3ea4ff;
}
.attendance-page .attendance-info-text span:first-child {
  height: 18px;
  border-bottom: 1px solid;
}

/* 螢幕寬度較窄 */
@media screen and (max-device-width: 320px) {
.attendance-page .clock-area{
    height: 40%;
}
.attendance-page .clock-canvas{
    width: 100px;
    height: 100px;
}
.attendance-page #clock {
    width: 100px;
    height: 100px;
}
.attendance-page .attendance-btn-area {
    margin-top: 0;
}
.attendance-page .attendance-info {
    margin-top: 10px;
}
.attendance-page .attendance-info-text {
    margin-top: 0;
}
}

/* 簽到詳情顯示 */
.list-ul {
  width: 100%;
}
.list-ul > .li-title {
  min-height: 14px;
  vertical-align: top;
}
.list-ul > .li-info {
  max-width: 60%;
  word-break: break-all;
}



