
.store[data-v-8c5c43a2] {
  background: #ffffff;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  margin: 0;
}
.main[data-v-8c5c43a2] {
  height: 100%;
  position: relative;
  /* height: calc(100vh - 64px); */
  /* overflow-y: scroll; */
}
.storeList[data-v-8c5c43a2] {
  /* padding: 106px 17px 16px; */
  /* height: calc(100% - 32px); */
  padding: 106px 17px 8px;
  width: calc(100% - 34px);
  height: calc(100vh - 247px);
  /* height: calc(100vh - 245px); */
  /* position: fixed; */
  /* top: 173px; */
}

/* 搜索區塊 */
.searchBlock[data-v-8c5c43a2] {
  padding: 8px 25px;
  background: #eeeeee;
}
.searchInput[data-v-8c5c43a2] {
  width: 100%;
  height: 33px;
  padding-left: 33px;
  border-radius: 4px;
  background: #e2e2e2 url('../../../assets/img/common/search@2x.png') no-repeat;
  background-size: 13px 13px;
  background-position: 13px;
  color: #bcbcbc;
  font-size: 14px;
  border: none;
  box-sizing: border-box;
  outline: none;
}

/* iphoneX 設置 */
@media only screen and (device-width: 375px)
  and (device-height: 812px)
  and (-webkit-min-device-pixel-ratio: 3) {
.storeList[data-v-8c5c43a2] {
      top: 173px;
      height: calc(100vh - 280px);
}
}



