
.common-select {
  width: 100%;
  height: 100%;
  color: #333333;
  font-size: 15px;
}
/* 下拉选单 */
.common-select .custom-select {
  position: relative;
  /* min-width: 250px; */
  width: 100%;
  height: 100%;
}
.common-select .custom-select select {
  display: none;
}
/* 下拉选单 被选中样式*/
.common-select .custom-select .select-selected {
  background: url("../assets/img/common/select_bg@2x.png") no-repeat;
  /* background-size: 250px 30px; */
  background-size: 100%;
  line-height: 30px;
  height: 100%;
  padding-left: 15px;
}
.common-select .custom-select .select-selected:after {
  position: absolute;
  content: "";
  top: 14px;
  right: 10px;
  width: 0;
  height: 0;
}
/* 下拉选单 (options):*/
.common-select .custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  /* width: 250px; */
  width: 100%;
  max-height: 50vh;
  left: 0;
  right: 0;
  z-index: 99;
  overflow-y: scroll;
}
.common-select .custom-select .select-items > div {
  position: relative;
  width: 100%;
  height: 30px;
}
/* 下拉选单 (options): */
.common-select .custom-select .select-items div {
  color: black;
  background: white;
  height: 30px;
  width: 100%;
  line-height: 30px;
  padding-left: 10px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  
  /* border-radius: 5px; */
  /* background: url("../assets/img/common/re_line_cutoff_short.png") no-repeat; */
  /* background-size: 100%; */
  background-position: bottom;
  box-sizing: border-box;
}
/* 关闭下拉选单 */
.common-select .custom-select .select-hide {
  display: none;
}
/* 不可修改 */
.common-select .custom-select .no-edit{
  background: #ffffff;
  padding: 0;
}



