}
/* 圖片 外框 */
.content-bg[data-v-1df13e47] {
  height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}
/* 圖片 */
.content-bg img[data-v-1df13e47] {
  width: 90%;
