.info[data-v-14213b66] {
  background: url('../../../assets/img/campaign/hd_list_bg@2x.png') no-repeat;
  background-size: 100% 100%;
  padding: 11.5px 28.5px 15.5px 13px; /* 右陰影約3px、下陰影約4px*/
  margin-bottom: 7px; /* 11px - 下陰影約4px*/
  position: relative;
  font-size: 0;
}
.name[data-v-14213b66] {
  font-size: 15px;
  color: #333333;
  width: 65%;
}
.date[data-v-14213b66] {
  font-size: 12px;
  color: #666666;
  width: 30%;
}
.name[data-v-14213b66], .date[data-v-14213b66] {
  display: inline-block;
  vertical-align: middle;
}
.arrowRight[data-v-14213b66] {
  height: 14px;
  width: 8.5px;
  position: absolute;
  top: 50%;
  right: 20px;  /* 17px + 右陰影約3px*/
  -webkit-transform: translateY(-9px);
          transform: translateY(-9px); /* 7px + (hd_list_bg@2x.png下陰影約4px)/2 */
}



