
.container .list_container[data-v-d8f81a54] {
  max-height: 300px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.customer-check-in .list-box[data-v-d8f81a54]{
  box-sizing: border-box;
  height: 51px;
  width: 91%;
  display: block;
  margin: 8px auto;
  background: url('../../../assets/img/common/home_list_bg@2x.png');
  background-size: 100% 100%;
  position: relative;
}
.customer-check-in .list-box ul[data-v-d8f81a54]{
  /* width: 60%; */
  box-sizing: border-box;
  margin-left: 13px;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-padding-start: 0;
  height: 100%;
}
.customer-check-in .list-box .list-title[data-v-d8f81a54] {
  font-size: 15px;
  color: #333333;
  height: 15px;
  vertical-align: middle;
}
.customer-check-in .list-box .list-addr[data-v-d8f81a54] {
  font-size: 12px;
  color: #666666;
  height: 15px;
  vertical-align: middle;
}
.customer-check-in .list-box .radio-div[data-v-d8f81a54] {
  top: 29%;
  position: absolute;
  max-width: 17px;
  max-height: 16px;
  right: 17px;
}

/* radio button 樣式 */
.customer-check-in .list-box input[type="radio"][data-v-d8f81a54]{
	display: none;
}
.customer-check-in .list-box input[type="radio"] + label[data-v-d8f81a54]{
	display: inline-block;
  /* background: url(../image/choose_n.png) no-repeat; */
  background: url('../../../assets/img/common/choose_n@2x.png') no-repeat;
	background-size: 16px 16px;
	background-position: left;
}
.customer-check-in .list-box label[data-v-d8f81a54]{
	padding-left: 20px;
	padding-right: 10px;
}
.customer-check-in .list-box input[type="radio"]:checked + label[data-v-d8f81a54]{
	display: inline-block;
	/* background: url(../image/choose_y.png) no-repeat; */
	background: url('../../../assets/img/common/choose_y@2x.png') no-repeat;
	background-size: 16px 16px;
	background-position: left;
}

