
.common-navbar {
  min-height: 64px;
}
.common-navbar .nav-bar {
  padding-top: 40px;
  height: 44px;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  color: white;
}
.common-navbar .nav-bar ul {
  width: 98vw;
  height: 44px;
  /* width: 100%;
  height: 64px; */
  padding-left: calc(1vw + 2px);
  margin-top: 0px;
  list-style-type: none;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.common-navbar .nav-bar ul li {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding-top: 0px;
  width: 33%;
  height: 44px;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  /* float: left; */
}
.common-navbar .nav-bar ul li:nth-child(1) {
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.common-navbar .nav-bar ul li:nth-child(2) {
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.common-navbar .nav-bar ul li:nth-child(3) {
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

/* header - title */
.common-navbar .middle-title-block_only-text {
  text-align: center;
}
.common-navbar .middle-title-block_only-text a {
  line-height: 44px;
  font-size: 18px;
}
.common-navbar .left-btn_only-text {
  position: relative;  
  display: -webkit-box;  
  display: -webkit-flex;  
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  left: 8px;
}
.common-navbar .left-btn_only-text a {
  position: relative;
  /* line-height: 84px; */
  line-height: 44px;
  font-size: 16px;
  left: 5px;
}
.common-navbar .left-btn_only-text img {
  position: relative;
  height: 16px;
  top: 14px;
  /* top: 34px; */
}
.common-navbar .left-btn-block_img-text {
  position: relative;
  /* width: 150%; */
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.common-navbar .left-btn-block_img-text a {
  font-size: 12px;
  color: white;
}
.common-navbar .left-btn-block_img-text img {
  height: 20px;
  width: 20px;
}
.common-navbar .right-btn-block_img-text {
  position: relative;
  /* width: 150%; */
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  /* top: 20px; */
}
.common-navbar .right-btn-block_img-text img {
  /* position: absolute;
  right: 18px;
  top: 2px; */
  height: 20px;
  width: 20px;
}
.common-navbar .right-btn-block_img-text a {
  /* position: absolute;
  height: 20px;
  right: 2px;
  top: 26px; */
  font-size: 12px;
  color: white;
}
.common-navbar .right-btn_only-text {
  text-align: right;
}
.common-navbar .right-btn_only-text a {
  position: relative;
  height: 3vh;
  color: white;
  line-height: 44px;
  /* line-height: 84px; */
  font-size: 16px;
}
.common-navbar .right-btn-block_only-img {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.common-navbar .right-btn-block_only-img img {
  position: relative;
  right: 10px;
  top: 10px;
  height: 20px;
}

